.styled-table{
    direction: rtl;
    /* border-collapse: collapse; */
    border: black;
    border: 1px;
    margin: auto;
    font-size: 0.9em;
    font-family: sans-serif;
    max-width: 800px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.styled-table thead tr{
    background-color: #327f07;
    color: #ffffff;
    text-align: right;
}

.styled-table th,
.styled-table td {
    padding: 12px 15px;
    text-align: center;
}


.styled-table tbody tr {
    border-bottom: 1px solid #dddddd;
}

.styled-table tbody tr:nth-of-type(even) {
    background-color: #f3f3f3;
}

.styled-table tbody tr:last-of-type {
    border-bottom: 2px solid #009879;
}

.btn {
    border: none;
    color: white;
    padding: 5px 8px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    border-radius: 5px;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
}

.btn-edit {
    background-color: #81a135;
}

.btn-delete {
    background-color: #f44336;
}

.btn-view{
    background-color: #9ed9db;
    color: black;
}

.data{
    display: flex;
    direction: rtl;

}

.filterTable {
    margin: auto;
    padding: 1em;
}

.check{
    width: 50%;
    padding: 12px 20px;
    margin: 4px 2px;

}

.search{
    position: relative;
    margin: auto;
    width: 350px;
    direction: rtl;
}

input[type="button"] {
    background-color: #917c46;
    color: white;
}